tem_description p{color:var(--post-card-description-color)}@media screen and (max-width: 768px){.posts.posts_columns .posts_item_description{font-size:18px}}.posts.posts_columns .posts_item_img{width:100%;height:clamp(240px,26.04vw,375px);border-radius:6px;overflow:hidden;display:block}.posts.posts_columns .posts_item_img img{width:100%;height:100%;object-fit:cover;user-select:none}